import React from "react";
import Card from "@mui/material/Card";
import CardHeader from "@mui/material/CardHeader";
import CardMedia from "@mui/material/CardMedia";
import CardContent from "@mui/material/CardContent";
import CardActions from "@mui/material/CardActions";
import Avatar from "@mui/material/Avatar";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import { red } from "@mui/material/colors";

import FavoriteIcon from "@mui/icons-material/Favorite";
import ShareIcon from "@mui/icons-material/Share";
import MoreVertIcon from "@mui/icons-material/MoreVert";
const UserComponent = (id, User) => {
  const Width = 345;
  const Height = 300;
  // let Favorite = {
  //   IsFavorite: false,
  //   Value: "",
  // };
  // const [FavoriteIconValue, setFavoriteIconValue] = useState(Favorite);
  // let ChangeValueFavoriteIcon = () => {
  //   if (!Favorite.IsFavorite) {
  //     Favorite = {
  //       IsFavorite: true,
  //       Value: "danger",
  //     };
  //     setFavoriteIconValue(Favorite);
  //   } else {
  //     Favorite = {
  //       IsFavorite: false,
  //       Value: "",
  //     };
  //     setFavoriteIconValue(Favorite);
  //   }
  // };
  return (
    <Card sx={{ width: Width }} className="m-2" key={id}>
      <CardHeader
        avatar={
          <Avatar sx={{ bgcolor: red[500] }} aria-label="recipe">
            {User.username.slice(0, 1)}
          </Avatar>
        }
        action={
          <IconButton aria-label="settings">
            <MoreVertIcon />
          </IconButton>
        }
        subheader={User.name}
        title={User.username}
      />
      <CardMedia
        component="img"
        height={Height}
        image={`https://via.placeholder.com/${Width}x${Height}/${
          (id + 3) * (id + 3) * (id + 3)
        }`}
        alt="Paella dish"
      />
      <CardContent>
        <Typography variant="body" color="text.secondary">
          <ol>
            <li>Email : {User.email}</li>
          </ol>
        </Typography>
      </CardContent>
      <CardActions disableSpacing>
        <IconButton
          aria-label="add to favorites"
          // color={FavoriteIconValue.Value}
          // onClick={ChangeValueFavoriteIcon()}
        >
          <FavoriteIcon />
        </IconButton>
        <IconButton aria-label="share">
          <ShareIcon />
        </IconButton>
      </CardActions>
    </Card>
  );
};

export default UserComponent;
